<template>
	<view>
    <!-- 输入信息 -->
    <view v-if="addInfo" class="nvue-page-root">
      <view class="car_info">
        <view class="car_info_header">
          <view class="car_info_name">车主信息</view>
          <view class="car_info_edit" @click="editUserInfo()">修改信息</view>
        </view>
        <view class="car_info_all">
          <view class="car_info_all_list">
            <view class="car_info_all_label">车主姓名：</view>
            <view class="car_info_all_content">{{ params.name }}</view>
          </view>
          <view class="car_info_all_list">
            <view class="car_info_all_label">联系电话：</view>
            <view class="car_info_all_content">{{ params.phone }}</view>
          </view>
          <view class="car_info_all_list">
            <view class="car_info_all_label">紧急联系电话：</view>
            <view class="car_info_all_content">{{ params.emergencyPhone }}</view>
          </view>
        </view>
      </view>

      <view class="my_car">
        <view class="my_car_header">
          <view class="my_car_name">我的车辆</view>
          <view class="my_car_all" @click="carAdd()">添加车辆</view>
        </view>
        <view :class="item.active == 0 ? 'activeContent':'my_car_list'" v-for="(item,index) in carItems" :key="item.id" @click="openActive(item,index)">
          <view class="my_car_list_left">
            <image mode="scaleToFill" :src='item.carBrandDTO.logoUrls' />
          </view>
          <view class="my_car_list_center">
            <view class="my_car_list_center_brand">{{ item.carBrandDTO.name }}</view>
            <view class="my_car_list_center_model">{{ item.carBrandDTO.model }}</view>
            <view class="my_car_list_center_plate">{{ item.licensePlate }}</view>
          </view>
          <view class="my_car_list_right">
            <view><image mode="scaleToFill" src="../../static/icon/insurance3x.png" />{{ item.insuranceId }}</view>
            <view v-if="item.active == 0" class="active">
              <image class="active_img" mode="scaleToFill" src="../../static/icon/active3x.png" />已选中</view>
          </view>
        </view>
      </view>
      <view class="uni-common-mt">
        <view class="uni-form-item uni-column">
          <view class="title"><text class="uni-form-item__title"><text>*</text>救援地点</text></view>
          <view class="uni-input-wrapper">
            <input class="uni-input" v-model="listQuery.helpAddress" disabled='disabled' type="number" placeholder="请输入" style="padding-right:80rpx"/>
            <image class="uni_icon_adress" mode="scaleToFill" src="../../static/icon/adress3x.png" @click="chooseLocation(0)"/>
          </view>
        </view>
        <view class="uni-form-item uni-column">
          <view class="title"><text class="uni-form-item__title"><text>*</text>情况描述</text></view>
          <view class="uni-input-wrapper">
            <textarea class="uni-textarea" v-model="listQuery.description"/>
          </view>
        </view>
        <view class="uni-form-item uni-column">
          <view class="title"><text class="uni-form-item__title"><text>*</text>拍照上传</text></view>
          <upload-image
            :dataList="dataListUpload" 
            uploadUrl="http://localhost:3000/upload"
            deleteUrl='http://localhost:3000/upload'
            :types="types"
            @successImage="successImage"
            @successVideo="successvideo"
            @localhostImage="localhostImage" 
          />
        </view>
      </view>
      <view class="submit">
        <button @click="saveInfo"> 确认完成，下一步 </button>
      </view>
    </view>

    <view v-else class="uni-margin-wrap">
			<swiper v-if="imgListItem.length > 0" class="swiper" circular :indicator-dots="indicatorDots" :autoplay="autoplay" :interval="interval" :duration="duration">
				<swiper-item v-for="item in imgListItem" :key="item.id">
				  <image class="image" mode="scaleToFill" :src="item" />
				</swiper-item>
			</swiper>
      <view class="info">
        <view class="content" v-for="item in listItems" :key="item.id">
          <view class="content_name">{{ item.stepsName }}</view>
          <view class="content_body">{{ item.stepsContent }}</view>
        </view>
      </view>
       <view class="submit" @click="goAddInfo">
        <button>{{ submitName }}</button>
      </view>
		</view>
	</view>
</template>
<script>
import datetimePicker from '../../components/datatime-picker/datetime-picker.vue';
import UploadImage from '../../components/upload-image/upload-image.vue';
import qqmapsdk from '@/utils/qqmap-wx-jssdk';
// import {fetchGetPageList as abc} from '../../utils/api';
import * as util from '@/common/util.js'
  let formatLocation = util.formatLocation
	export default {
    components: {
      datetimePicker,
      UploadImage
    },
		data() {
			return {
				indicatorDots: true,
				autoplay: true,
        addInfo: false,
				interval: 4000,
				duration: 500,
        dataListUpload: [],
        imgListItem: [], //../../static/index/bannerIndex.png
        listItems: [],
        index: 1,
        submitName: '确认完成，下一步',
        isAddress: true,
        title: 'input',
        defaultValue: '', // 默认值
        dataList: [],
				types: 'image',
        brandIndex: 0,
        modelIndex: 0,
        companyIndex: 0,
        inputValue: '',
        carItems: [],
        params:{
          name: null,
          phone: null,
          emergencyPhone: null,
        },
        listQuery:{
          helpAddress: null,
          description: null,
          fileId: [],
          localhostFileId:[]
        }
			}
		},
    onLoad(options){
      console.log(this.index)
      this.$http.parkingCarDetail({'scenario':options.scenario}).then(res=>{
        console.log(res)
        this.dataList = res.data && res.data.parkingCarDetailDTOS
        if(this.dataList[0] && this.dataList[0].type == 1){
          this.addInfo = false
          this.listItems.push(res.data && res.data.parkingCarDetailDTOS[0])
          this.imgListItem = res.data && res.data.parkingCarDetailDTOS[0] && res.data.parkingCarDetailDTOS[0].stepsFileUrls
        }else if(this.dataList[0] && this.dataList[0].type == 2){
          this.addInfo = true
        }
        console.log(this.listItems)
      })
      uni.setStorageSync('rescueAddress',null)
      let userId = uni.getStorageSync('ID')
      this.$http.carOwnerDetail({'createUserId':userId}).then(res=>{
        this.params = res.data && res.data[0]
        console.log(res)
        let carDTO = res.data && res.data
        let carInfo = []
        console.log('123--',carDTO)
        carDTO.forEach(e => {
          let carList = {
            licensePlate: null,
            insuranceId : null,
            ownerId: null,
          }
          let carInfoDetail = e.carBrandDTO
          carList.carBrandDTO = carInfoDetail
          carList.licensePlate = e.licensePlate // 车牌号
          carList.insuranceId = (e.insuranceCompany && e.insuranceCompany.name) ? e.insuranceCompany.name : '-' //保险公司名称
          carList.ownerId = e.createUserId //创建人ID
          carList.ownerName = e.name //
          carList.phone = e.phone //
          carList.emergencyPhone = e.emergencyPhone //
          carInfo.push(carList)
        })
        carInfo[0].active = 0
        this.carItems = carInfo
        this.listQuery.ownerName = carInfo[0].ownerName
        this.listQuery.phone = carInfo[0].phone
        this.listQuery.licensePlate = carInfo[0].licensePlate
        
        // listQuery.insuranceId = carInfo[0].insuranceId
        this.listQuery.ownerId = carInfo[0].ownerId
        this.listQuery.carName = carInfo[0].carBrandDTO && carInfo[0].carBrandDTO.name
        this.listQuery.carModel = carInfo[0].carBrandDTO && carInfo[0].carBrandDTO.model
        this.listQuery.carBrandId = carInfo[0].carBrandDTO && carInfo[0].carBrandDTO.id
        this.listQuery.emergencyPhone = carInfo[0].emergencyPhone
        
        console.log('carInfo =>',carInfo)
        console.log('listQuery =>',this.listQuery)
      })
      
      // uni.setStorageSync('rescueAddress',null)
      uni.getLocation({
        type: 'wgs84',
    　　 geocode:true,
        altitude: true,
        isHighAccuracy: true,

        success: function (res) {
          console.log(res);
          var that = this;
          var map = new qqmapsdk({
            key: 'PLUBZ-U3CRX-25I4F-ZW5YC-HSCWO-KKBQ2'
            // key: 'TXZBZ-HPMWU-4QIVM-2ISK7-2L7GK-7XBE4'
            // key:'CNGBZ-3IWKP-QVIDZ-LOA2N-FJKTO-YCBR6'

          })
          //通过这个方法来实现经纬度反推省市区
          map.reverseGeocoder({
            location: {
              latitude: res.latitude,
              longitude: res.longitude
            },
            success: function (res) {
              uni.setStorageSync('rescueStartAddress',res.result.address + res.result.formatted_addresses.recommend)
            }
          })
        }
      })
    },
    onShow() {
      uni.setStorageSync('rescueAddress',null)
      let userId = uni.getStorageSync('ID')
      this.$http.carOwnerDetail({'createUserId':userId}).then(res=>{
        this.params = res.data && res.data[0]
        console.log(res)
        let carDTO = res.data && res.data
        let carInfo = []
        console.log('123--',carDTO)
        carDTO.forEach(e => {
          let carList = {
            licensePlate: null,
            insuranceId : null,
            ownerId: null,
          }
          let carInfoDetail = e.carBrandDTO
          carList.carBrandDTO = carInfoDetail
          carList.licensePlate = e.licensePlate // 车牌号
          carList.insuranceId = (e.insuranceCompany && e.insuranceCompany.name) ? e.insuranceCompany.name : '-' //保险公司名称
          carList.ownerId = e.createUserId //创建人ID
          carList.ownerName = e.name //
          carList.phone = e.phone //
          carList.emergencyPhone = e.emergencyPhone //
          carInfo.push(carList)
        })
        carInfo[0].active = 0
        this.carItems = carInfo
        this.listQuery.ownerName = carInfo[0].ownerName
        this.listQuery.phone = carInfo[0].phone
        this.listQuery.licensePlate = carInfo[0].licensePlate
        
        // listQuery.insuranceId = carInfo[0].insuranceId
        this.listQuery.ownerId = carInfo[0].ownerId
        this.listQuery.carName = carInfo[0].carBrandDTO && carInfo[0].carBrandDTO.name
        this.listQuery.carModel = carInfo[0].carBrandDTO && carInfo[0].carBrandDTO.model
        this.listQuery.carBrandId = carInfo[0].carBrandDTO && carInfo[0].carBrandDTO.id
        this.listQuery.emergencyPhone = carInfo[0].emergencyPhone
        
        console.log('carInfo =>',carInfo)
        console.log('listQuery =>',this.listQuery)
        uni.getLocation({
          type: 'wgs84',
      　　 geocode:true,
          altitude: true,
          isHighAccuracy: true,

          success: function (res) {
            console.log(res);
            var that = this;
            var map = new qqmapsdk({
              key: 'PLUBZ-U3CRX-25I4F-ZW5YC-HSCWO-KKBQ2'
              // key: 'TXZBZ-HPMWU-4QIVM-2ISK7-2L7GK-7XBE4'
              // key:'CNGBZ-3IWKP-QVIDZ-LOA2N-FJKTO-YCBR6'

            })
            //通过这个方法来实现经纬度反推省市区
            map.reverseGeocoder({
              location: {
                latitude: res.latitude,
                longitude: res.longitude
              },
              success: function (res) {
                uni.setStorageSync('rescueStartAddress',res.result.address)
              }
            })
          }
        })
      })
    },
    updated(){
      if(this.isAddress){
        if(uni.getStorageSync('rescueAddress') == null){
          this.listQuery.helpAddress = uni.getStorageSync('rescueStartAddress')
        }else{
          this.listQuery.helpAddress = uni.getStorageSync('rescueAddress')
        }
        this.isAddress = false
      }
      
    },
		methods: {
      goAddInfo: function(){
        let count = this.index++
        console.log(count)
        console.log(this.dataList[count])
        if(count < this.dataList.length-1){
          if(this.dataList[count] && this.dataList[count].type == 1){
            this.addInfo = false
            this.imgListItem = this.dataList[count] && this.dataList[count].stepsFileUrls
            this.listItems = []
            this.listItems.push(this.dataList[count])
          }else if(this.dataList[count] && this.dataList[count].type == 2){
            this.addInfo = true
          }
        }else if(count = this.dataList.length-1){
          this.submitName = '完成'
          if(this.dataList[count] && this.dataList[count].type == 1){
            this.addInfo = false
            this.imgListItem = this.dataList[count] && this.dataList[count].stepsFileUrls
            this.listItems = []
            this.listItems.push(this.dataList[count])
          }else if(this.dataList[count] && this.dataList[count].type == 2){
            this.addInfo = true
            console.log(this.addInfo)
          }
        }
        if(this.index > this.dataList.length){
          console.log(this.dataList[count].type)
          if(this.listQuery.description == null){
            uni.switchTab({
              url:'/pages/rescue/rescueSelect'
            })
          }else{
            uni.navigateTo({
              url:'/pages/rescue/rescueInfo?params='+JSON.stringify(this.listQuery)
            })
          }
          // uni.navigateTo({
          //   url:'/pages/rescue/rescueAdd'
          // });
        }
      },
      successImage(e){
        let imgArray = e && JSON.parse(e.data)
        this.listQuery.fileId.push(imgArray.data && imgArray.data.id)
			},
      localhostImage(e){
        this.listQuery.localhostFileId.push(e)
        console.log('this.listQuery.localhostFileId =>',this.listQuery.localhostFileId)
      },
      chooseLocation: function (e) {
        uni.chooseLocation({
          success: (res) => {
            this.hasLocation = true,
            // this.location = formatLocation(res.longitude, res.latitude),
            this.listQuery.helpAddress = res.address + res.name
          }
        })
     },
      saveInfo: function(){
        // console.log(this.listQuery)
        if(this.listQuery.helpAddress == null){
          uni.showToast({
            title:"救援地址不能为空" ,
            icon:'none',
	          duration: 2000
          })
        }else if(this.listQuery.description == null){
          uni.showToast({
            title:"情况描述不能为空" ,
            icon:'none',
	          duration: 2000
          })
        }else if(this.listQuery.fileId.length == 0){
          uni.showToast({
            title:"请拍照上传" ,
            icon:'none',
	          duration: 2000
          })
        }else{
          let img = this.listQuery.fileId
          this.listQuery.fileId = img.toString()
          this.listQuery.type = uni.getStorageSync('TYPE')
          console.log(this.listQuery)
          this.goAddInfo()
          // uni.navigateTo({
          //   url:'/pages/rescue/rescueInfo?params='+JSON.stringify(this.listQuery)
          // })
        }
      },
      editUserInfo(){
        let params = this.params
        uni.navigateTo({
          url:'/pages/user/edit?params='+JSON.stringify(params)
        })
      },
      carAdd(){
        let params = this.params
        uni.navigateTo({
          url:'/pages/user/addCar?params='+JSON.stringify(params)
        })
      },
      openActive(item,index){
        let active = this.carItems[index]
        console.log('active =>',active)
        this.listQuery.ownerName = active.ownerName
        this.listQuery.phone = active.phone
        this.listQuery.licensePlate = active.licensePlate
        this.listQuery.insuranceId = active.insuranceId
        this.listQuery.carName = active.carBrandDTO && active.carBrandDTO.name
        this.listQuery.carModel = active.carBrandDTO && active.carBrandDTO.model
        this.listQuery.emergencyPhone = active.emergencyPhone
        this.listQuery.carBrandId = active.carBrandDTO && active.carBrandDTO.id

        console.log('licensePlate =>',this.listQuery.licensePlate)
        let carList = this.carItems
          carList.forEach(e => {
            e.active = null
          });
        this.$nextTick(() =>{
          this.carItems[index].active = 0
          this.$forceUpdate()                                  
        })
        
      },
		}
	}
</script>

